@import url(https://fonts.googleapis.com/css2?family=Karla:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;700&display=swap);
html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: -apple-system, 'Segoe UI Light', 'Karla', BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html, body, #root, .App {
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

#root {
  --bg: #1E272E;
  /* --bg: rgb(203, 253, 219); */
  /* --bg: white; */
  --hover-color: coral;
  /* --hover-color:   rgb(146, 46, 121); */
  --text-color: white;
  background-color: var(--bg);
  color: var(--text-color);
  /* color: #333; */
}

button {
  cursor: pointer;
  background-color: transparent;
  border: none;
  font: inherit;
  color: inherit;
  padding: 0;

  transition: all 0.3s linear;

}

button:hover {
  -webkit-filter: brightness(80%);
          filter: brightness(80%);
}

button.link {
  /* padding: 0; */
  text-decoration: underline;
  font-style: italic;
}

button.link:hover{
  color: var(--hover-color);
  -webkit-filter: brightness(100%);
          filter: brightness(100%);
}

button.info-nav-bar:hover {
  color: coral;
}

h1 {
  font-size: 3rem;
  font-weight: bold;
  margin-top: 0;
  padding-top: 2rem;
}

h2 {
  font-size: 2rem;
  font-weight: bold;
}

svg {
  height: 3rem;
}

.std-btn {
  border: 2px solid var(--hover-color);
  padding: 0.5rem 1rem;
  min-width: 12rem;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  color: white;
  border-radius: 5px;
  font-weight: 700;
  font-family: 'Karla', Geneva, Tahoma, sans-serif;
}

.std-btn:hover {
  background-color: var(--hover-color);
  color: var(--bg);
  -webkit-filter: none;
          filter: none;
}

.hflex {
  display: flex;
}

.hidden {
  display: none !important;
}
/** GENERAL **/

ul.tech-stack {
  list-style: none;
  margin: 0;
  padding: 0;
  font: inherit;
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
  flex-wrap: wrap;
  
  padding-bottom: 3rem;
}

/** INTRO **/

#intro {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  text-align: left;
  height: 100%;
  padding: 10%;
  font-size: 2rem;
}

#intro > .heading {
  font-size: 3rem;
  font-weight: bold;
  padding-bottom: 1rem;
}

#intro > .heading > img {
  height: 2.5rem;
}

#intro > .content {
  padding-bottom: 0.5rem;
}

#about {
  
}

img[name="Yay"] {
  -webkit-animation: rotate-head ease-in-out 3s;
          animation: rotate-head ease-in-out 3s;
  transform: rotate(30%);
}

@-webkit-keyframes rotate-head {
  0% {
    transform: rotate(40deg);
  }

  33% {
    transform: rotate(-10deg);
  }

  66% {
    transform: rotate(20deg);
  }
}

@keyframes rotate-head {
  0% {
    transform: rotate(40deg);
  }

  33% {
    transform: rotate(-10deg);
  }

  66% {
    transform: rotate(20deg);
  }
}

/** INFO CARD **/

.info-pane {
  background-color: var(--bg);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  font-size: 1.5rem;
  text-align: left;

  transition: all 1s ease-in-out;
  z-index: 50;
  overflow: auto;
}

.info-pane-content {
  padding: 3rem;
  display: flex;
  flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
  /* overflow: auto; */
  height: 100%;
}

.info-pane-content > div:last-child {
  padding-bottom: 3rem;
}


.tech-stack img {
  height: 2.5rem;
  transition: transform 0.2s ease-in-out;
}

.tech-stack img:hover {
  transform: scale(1.5);
}

.tech-stack li {
  --scale: 0;
  position: relative;
}

.tech-stack li::after {
  position: absolute;
  bottom: -1rem;
  left: 50%;
  transform: translateX(-50%) translateY(100%) scale(var(--scale));
  transition: 0.2s transform ease-in-out;
  transform-origin: top center;
  content: attr(data-name);
  padding: 0.5rem;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  max-width: 100vw;
  background-color: white;
  color: black;
  border-radius: 7px;
  z-index: 1;
}

.tech-stack li:hover {
  --scale: 1;
}

.info-nav-bar {
  position: absolute;
  top: 10px;
  left: 5px;
  z-index: 1;
}

.info-pane-hidden {
  transform: translateY(-100%);
  /* position: hidden;
  visibility: hidden; */
  opacity: 0;
}

/** CONTACT **/

.contact-entry {
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
  align-items: center;
}

a.contact-entry {
  color: var(--text-color);
  /* background-color: blue; */
  text-decoration: none;
  transition: all 0.3s linear;
}

a.contact-entry:hover {

  color: var(--hover-color);
}

.thumbnail { 
  background: rgb(241,202,255); /* fall back */
  background: linear-gradient(180deg, rgba(241,202,255,1) 0%, rgba(123,228,255,1) 100%); 
  position: relative;
  border-radius: 10px;
}

.thumbnail-text {
  height: 100%;
  font-size: 5rem;
  font-family: 'Times New Roman', Times, serif, sans-serif;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #333;
}

.thumbnail img {
  border-radius: inherit;
  object-fit: contain;
  height: 100%;
  width: 100%;
}

.thumbnail-description {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 6rem;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 1.5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  
  border-bottom-left-radius: inherit;
  border-bottom-right-radius: inherit;
}

.thumbnail-description div {
  word-wrap: break-word;
  overflow-wrap: break-word;
  text-overflow: ellipsis;
  /* text-overflow: clip; */
  
  white-space: nowrap;
  overflow: hidden;
}

.thumbnail-title {
  font-weight: bold;
  font-size: 1.5rem;
}

button.thumbnail {
  text-align: left;
}

/** PROJECT **/

.project {
  background-color: var(--bg);
  padding: 2rem;
}

.project-list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 350px);
  grid-gap: 2rem;
  gap: 2rem;
}

.project-detail-snippet {
  font-size: 1rem;
  font-style: italic;
}

/** PROJECT DETAIL **/
.demo-list {
  list-style-type: none;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-auto-rows: auto;
  justify-items: center;
}

@media screen and (max-width: 1200px) {
  .demo-list {
    grid-template-columns: repeat(1, 1fr);
  }
}
